.advantages {
  margin: 0;
  padding: 115px 0 114px 0;

  background-color: $light-gray;

  h2 {
    margin: 0 0 66px 0;
    padding: 0;

    font-family: $font--bebasneue-regular;
    font-size: 49px;
    line-height: 49px;
    font-style: normal;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 0.04em;

    color: $blue;
  }

  h3 {
    margin: 0 0 7px 0;
    padding: 0;

    font-family: $font--roboto-bold;
    font-size: 22px;
    line-height: 33px;

    color: $blue;
  }

  p {
    margin: 0 10px 0 0;
    padding: 0;

    font-family: $font--roboto-light;
    font-size: 16px;
    line-height: 22px;

    color: $blue;
  }

  @media (max-width: $desktop-width) {
    padding: 97px 0 93px 0;

    h2 {
      margin: 0 0 45px 0;

      font-size: 42px;
      line-height: 42px;
    }

    h3 {
      margin: 0 0 3px 0;

      font-size: 20px;
      line-height: 30px;
    }
  }

  @media (max-width: $tablet-width) {
    padding: 67px 0 63px 0;

    h2 {
      margin: 0 0 52px 0;

      font-size: 32px;
      line-height: 32px;
    }

    h3 {
      text-align: center;
    }

    p {
      margin: 0;

      text-align: center;
    }
  }
}

.advantages__group {
  margin: 0 auto;

  width: 100%;
  max-width: 1040px;
}

.advantages__space-and-parking,
.advantages__comfort-and-experience {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @media (max-width: $tablet-width) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}

.advantages__space-and-parking {
  @media (max-width: $desktop-width) {
    margin: 0 0 43px 0;
  }
}

.advantages__comfort-and-experience {
  margin: -152px 0 0 0;

  @media (max-width: $desktop-width) {
    margin: 0;
  }
}

.advantages__space,
.advantages__parking,
.advantages__comfort,
.advantages__experience {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;

  width: 100%;
  max-width: 340px;

  @media (max-width: $desktop-width) {
    width: 48%;
  }

  @media (max-width: $tablet-width) {
    flex-direction: column;

    width: 100%;
  }
}

.advantages__space,
.advantages__parking {
  border-bottom: 2px solid $transparent-crimson;

  @media (max-width: $desktop-width) {
    border: none;
  }
}

.advantages__space,
.advantages__comfort {
  margin: 0 15% 0 0;

  p {
    margin: 0;
  }

  @media (max-width: $desktop-width) {
    margin: 0;
  }
}

.advantages__parking,
.advantages__experience {
  margin: 0 0 0 15%;

  @media (max-width: $desktop-width) {
    margin: 0;
  }
}

.advantages__space {
  margin: 0 15% 0 0;
  padding: 0 0 76px 2.8%;

  @media (max-width: $desktop-width) {
    margin: 0;
    padding: 0 25px 0 0;
  }

  @media (max-width: $tablet-width) {
    margin: 0 0 40px 0;
    padding: 0 55px;
  }
}

.advantages__parking {
  padding: 0 0 76px 0.2%;

  @media (max-width: $desktop-width) {
    padding: 0;
  }

  @media (max-width: $tablet-width) {
    padding: 0 30px;
  }
}

.advantages__comfort {
  padding: 74px 0 0 2%;

  @media (max-width: $desktop-width) {
    padding: 0;
  }

  @media (max-width: $tablet-width) {
    margin: 0 0 40px 0;
    padding: 0 35px;
  }
}

.advantages__experience {
  padding: 74px 0 0 0.3%;

  @media (max-width: $desktop-width) {
    padding: 0 0 0 0.3%;
  }

  @media (max-width: $tablet-width) {
    padding: 0 30px;
  }
}

.advantages__trainers {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  margin: -155px auto 0 auto;
  padding: 35px 33px 60px 33px;

  width: 240px;

  background-color: $intensive-blue;

  h3,
  p,
  span {
    color: $white;

    text-align: center;
  }

  p {
    margin: 0;
  }

  h3 {
    position: relative;
    top: -15px;

    margin: 0 0 18px 0;

    font-family: $font--roboto-bold;
    font-size: 26px;
    line-height: 39px;
  }

  @media (max-width: $desktop-width) {
    margin: 0 auto 42px auto;
    padding: 14px 130px 37px 130px;

    width: 442px;

    h3 {
      top: -20px;

      margin: 0;

      font-size: 22px;
      line-height: 33px;
    }

    p {
      position: relative;
      top: -3px;
    }
  }

  @media (max-width: $tablet-width) {
    margin: 0 0 47px 0;
    padding: 19px 55px 32px 55px;

    width: 100%;

    h3 {
      top: -24px;
    }

    p {
      top: -13px;
    }
  }
}

.advantages__number {
  font-family: $font--roboto-bold;
  font-size: 74px;
  line-height: 111px;
}

.advantages__data {
  span {
    position: relative;
    left: -2px;

    font-family: $font--roboto-regular;
    font-size: 14px;
    line-height: 21px;

    color: $light-blue;
  }

  @media (max-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    margin: 0 0 6px 0;

    span {
      left: 0;
    }
  }
}

.advantages__img {
  margin: 4px 33px 6px 0;
  padding: 0;

  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 0;

  &--space {
    width: 102px;
    height: 53px;

    background-image: url("../img/numbers/nine_hundred_1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/numbers/nine_hundred_2x.png");
    }
  }

  &--parking {
    width: 67px;
    height: 53px;

    background-image: url("../img/numbers/seventy_1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/numbers/seventy_2x.png");
    }
  }

  &--comfort {
    width: 103px;
    height: 53px;

    background-image: url("../img/numbers/three_hundred_fifty_1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/numbers/three_hundred_fifty_2x.png");
    }
  }

  &--experience {
    width: 65px;
    height: 53px;

    background-image: url("../img/numbers/five_1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/numbers/five_2x.png");
    }
  }

  @media (max-width: $desktop-width) {
    margin: 7px 33px 6px 0;

    &--space {
      width: 83px;
      height: 43px;
    }

    &--parking {
      width: 55px;
      height: 43px;
    }

    &--comfort {
      width: 85px;
      height: 43px;
    }

    &--experience {
      width: 53px;
      height: 43px;
    }
  }

  @media (max-width: $tablet-width) {
    margin: 0 0 5px 0;
  }
}
